<template>
	<div class="container">
		<b-nav class="wrap" pills>
			<b-nav-item
				exact
				exact-active-class="active"
				:to="{ path: '/m/items/ALL' }"
				>All</b-nav-item
			>
			<b-nav-item
				exact
				exact-active-class="active"
				v-for="cat in categories"
				:key="cat.id"
				:to="{ path: '/m/items/' + cat.description }"
			>
				{{ cat.description }}
			</b-nav-item>
		</b-nav>
		<div class="itemList">
			<div v-for="item in items" :key="item.item_no" class="item">
				<itemCell :item="item"></itemCell>
			</div>
		</div>
	</div>
</template>

<script>
import itemCell from '@/components/itemCell.vue';
import dataManager from '../dataManager';
export default {
	data() {
		return {
			categories: [],
			category: {},
			items: []
		};
	},
	components: {
		itemCell
	},
	computed: {},
	methods: {
		async getCategory() {
			if (this.$route.params.category === 'ALL') {
				this.category = { id: 'ALL', description: 'All' };
			} else {
				this.category = await dataManager.findCategory(
					this.$route.params.category
				);
			}
		},
		async getItems() {
			if (this.$route.params.category === 'ALL') {
				this.items = await dataManager.allItems();
			} else {
				this.items = await dataManager.findItemsByCategory(
					this.category.id
				);
				console.log('category loaded');
			}
		}
		// async getCategories() {
		//     dataManager.getAllCategories()
		//         .then(c => this.categories = c);
		// }
	},
	async mounted() {
		// await this.getCategories();
		dataManager.getAllCategories().then(c => (this.categories = c));
		await this.getCategory();
		await this.getItems();
	}
};
</script>

