<template>
	<div id="infocodePanel">
		<uomList
			v-if="itemUOMs.length > 0"
			:itemUOMs="itemUOMs"
			@uomChange="uomChange"
		/>
		<div v-for="code in activeInfocodes" :key="code.infocode_code">
			<infocodeCell :itemInfoCode="code" @subcodeChange="subcodeChange" />
		</div>
	</div>
</template>

<script>
import dataManager from '../../dataManager';
import infocodeCell from '@/components/info-codes/infocodeList';
import uomList from '@/components/info-codes/uomList';
export default {
	props: {
		item_no: {
			type: String,
			default() {
				return '';
			}
		}
	},
	components: {
		infocodeCell,
		uomList
	},
	data() {
		return {
			itemInfocodes: [],
			// selectedSubcodes: [],
			itemUOMs: [],
			activeUOMId: '',
			activeInfocodes: []
		};
	},
	computed: {
		// activeInfocodes() {
		// }
	},
	methods: {
		subcodeChange(sc) {
			this.$emit('subcodeChange', sc);
		},
		uomChange(uom) {
			this.$emit('uomChange', uom);

			// re-render infocodes base on uom
			let selectedUOM = uom.filter(u => u.state === true)[0];
			this.activeUOMId = selectedUOM.uomDetail.id;
			let _itemInfocodes = [];
			this.itemInfocodes.map(code => {
				if (!code.uom_code) {
					{
						_itemInfocodes.push(code);
					}
				}
				if (code.uom_code === selectedUOM.uomDetail.id) {
					_itemInfocodes.push(code);
				}
			});
			this.activeInfocodes = _itemInfocodes;
			// let infocodeWithUom = this.itemInfocodes.filter(i => i.uom_code === this.activeUOMId);
			// let infocodeGeneral = this.itemInfocodes.filter(i => (i.hasOwnProperty('uom_code') === false) || (i.uom_code === ''));
			// this.activeInfocodes =  infocodeWithUom.concat(infocodeGeneral);
		}
	},
	async mounted() {
		this.itemInfocodes = await dataManager.geItemInfocodes(this.item_no);
		this.itemUOMs = await dataManager.getItemUOMByNo(this.item_no);
	}
};
</script>

<style lang="scss" scoped></style>
