<template>
	<div id="qty-viewer">
		<div class="label">
			<div class="text">{{ quantity }}</div>
		</div>
	</div>
</template>

<script>
export default {
	props: {
		itemNo: {
			type: String,
			default() {
				return '';
			}
		}
	},
	methods: {
		add() {
			this.$emit('add', this.itemNo);
		},
		minus() {
			this.$emit('minus', this.itemNo);
		}
	},
	computed: {
		quantity() {
			let qty = 0;
			this.$store.getters.orderItems.map(itemLine => {
				if (itemLine.item_no === this.itemNo) {
					qty += itemLine.quantity;
				}
			});
			return qty;
		}
	}
};
</script>

