import mockCategories from '../../mock-data/koi/categories.json';
import mockItems from '../../mock-data/koi/items.json';
import mockInfocodes from '../../mock-data/koi/infoCodes.json';
import mockInfoSubcodes from '../../mock-data/koi/infoSubcodes.json';
import mockItemInfocodes from '../../mock-data/koi/itemInfocode.json';
import mockItemUom from '../../mock-data/koi/itemuom.json';
import mockUom from '../../mock-data/koi/uom.json';
import moment from 'moment';
var itemsByCat = {};
export default {
	async init() {
		let cats = await this.getAllCategories();
		for (const cat in cats) {
			let catId = cats[cat].id;
			let items = await this.findItemsByCatSource(catId);
			for (const item of items) {
				let priceTxt = item.unit_price;
				let itemUOMs = await this.getItemUOMs(item.item_no);
				if (itemUOMs.length > 0) {
					priceTxt = '';
					for (let i = 0; i < itemUOMs.length; i++) {
						priceTxt += itemUOMs[i].unit_price;
						priceTxt =
							i < itemUOMs.length - 1 ? priceTxt + '/' : priceTxt;
					}
				}
				item.unit_price_txt = priceTxt;
			}
			itemsByCat[catId] = items;
		}
		console.log('Datamanger Initialized');
		return;
	},
	async getAllCategories() {
		return mockCategories;
	},
	async allItemsByCat() {
		return itemsByCat;
	},
	async allItems() {
		return mockItems;
	},
	async findCategory(category) {
		return mockCategories.find(cat => cat.description === category);
	},
	async findItemsByCatSource(id) {
		return mockItems.filter(item => item.category === id);
	},
	async findItemsByCategory(id) {
		// return mockItems.filter(item => item.category === id);
		return itemsByCat[id];
	},
	async findItemsByNo(itemNo) {
		return mockItems.filter(item => item.item_no === itemNo)[0];
	},
	async getItemUOMByNo(itemNo) {
		return mockItemUom.filter(uom => uom.item_no === itemNo);
	},
	async getUOMById(id) {
		return mockUom.filter(uom => uom.id === id)[0];
	},
	async getTableSession() {
		return {
			tableNo: 1,
			sessionId: 'b3e26e38-d00f-11eb-b8bc-0242ac130003',
			entryTime: moment(),
			exitTime: null
		};
	},
	async getOrderNo() {},
	async geItemInfocodes(itemNo) {
		return mockItemInfocodes.filter(
			infocode => infocode.item_no === itemNo
		);
	},
	async getItemUOMs(itemNo) {
		return mockItemUom.filter(uom => uom.item_no === itemNo);
	},
	async getInfoCode(code) {
		return mockInfocodes.filter(infocode => infocode.code === code)[0];
	},
	async getInfoSubcodes(code) {
		return mockInfoSubcodes.filter(subcodes => subcodes.code === code);
	},
	async placeOrderAndGetTransactionNo() {
		let numStr = Math.floor(Math.random() * 10).toString();
		return 'R000' + numStr;
	}
};
