<template>
	<div class="container">
		<h2 class="header">{{ category.description }}</h2>
		<div class="itemList">
			<div v-for="item in items" :key="item.item_no" class="item">
				<itemCell :item="item"></itemCell>
			</div>
		</div>
	</div>
</template>

<script>
import itemCell from '@/components/itemCell.vue';
import dataManager from '../dataManager';
export default {
	data() {
		return {
			perPage: 12,
			perRow: 6,
			currentPage: 1,
			category: {},
			items: []
		};
	},
	components: {
		itemCell
	},
	computed: {
		rows() {
			return this.items.length / this.perRow;
		}
	},
	methods: {
		async getCategory() {
			if (this.$route.params.category === 'ALL') {
				this.category = { id: 'ALL', description: 'All' };
			} else {
				this.category = await dataManager.findCategory(
					this.$route.params.category
				);
			}
		},
		async getItems() {
			if (this.$route.params.category === 'ALL') {
				this.items = await dataManager.allItems();
			} else {
				this.items = await dataManager.findItemsByCategory(
					this.category.id
				);
			}
		}
	},
	async mounted() {
		await this.getCategory();
		await this.getItems();
	}
};
</script>

