<template>
	<div class="qty-control">
		<div v-if="operation" @click="minus" class="button align-mid">
			<b-icon icon="dash"></b-icon>
		</div>
		<div class="label">
			<div class="text">{{ qty }}</div>
		</div>
		<div v-if="operation" @click="add" class="button align-mid">
			<b-icon icon="plus"></b-icon>
		</div>
	</div>
</template>

<script>
export default {
	props: {
		operation: {
			type: Boolean,
			default() {
				return false;
			}
		},
		qty: {
			type: Number,
			default() {
				return 0;
			}
		}
	},
	methods: {
		add() {
			this.$emit('add', this.itemNo);
		},
		minus() {
			this.$emit('minus', this.itemNo);
		}
	}
};
</script>

