<template>
<b-overlay :show="show" rounded="sm">
	<template #overlay>
		<div class="text-center">
			<b-icon icon="stopwatch" font-scale="3" animation="cylon"></b-icon>
			<p>{{ $t('pls-wait-label') }}</p>
		</div>
	</template>
	<div v-if="show" id="overlay">
	</div>
	<div v-if="!show" id="checkout-view">
		<h1>{{ $t('checkout-msg') }}</h1>
		<div id="qr-wrapper">
			<qr id="qr" :text="transactionId" :size="300" />
		</div>
		<div id="btn-wrapper">
			<b-button squared block @click="reload">{{ $t('reorder-btn-label') }}</b-button>
		</div>
	</div>
	</b-overlay>
</template>

<script>
import qr from 'vue-qr';
export default {
	components: {
		qr
	},
	data() {
		return {
			show: true
		};
	},
	methods: {
		async reload() {
			await this.$store.dispatch('resetOrder');
			this.$router.replace('/');
		}
	},
	computed: {
		transactionId() {
			return this.$store.getters.transactionId;
		}
	},
	async mounted() {
		await new Promise(resolve => setTimeout(resolve, 1000));
		this.show = false;
	}
};
</script>

