<script>
// @ is an alias to /src
import dataManager from '../dataManager';

export default {
	name: 'Initializer',
	components: {},
	data() {
		return {
			show: true
		};
	},
	methods: {
		sleep(ms) {
			return new Promise(resolve => setTimeout(resolve, ms));
		},
		async loadTableSession() {
			let ts = await dataManager.getTableSession();
			await this.$store.dispatch('setTableSession', ts);
			await this.sleep(1000);
			this.show = false;
			this.$router.replace(this.getPathByUserAgent());
		},
		async newMobileOrder() {
			await this.$store.dispatch('resetOrder');
			await this.sleep(1000);
			this.show = false;
			this.$router.replace('/menu');
		},
		getPathByUserAgent() {
			// let path = /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent) ?
			// 	'/m/items/ALL' : '/items/ALL';
			// return path;
			return '/menu';
		}
	},
	async mounted() {
		this.show = true;
		console.log('Initializer Init');
		await dataManager.init();
		this.newMobileOrder();
	}
};
</script>
