import Vue from 'vue';
import VueRouter from 'vue-router';
import Initializer from '../views/Initializer.vue';
import MobileMenu from '../views/menu/no-tab/mobileMenuNoTab';
import ItemList from '../components/ItemList.vue';
import ItemListMobile from '../components/ItemListMobile.vue';
import Checkout from '../views/menu/no-tab/checkoutView';
import { BootstrapVue, BootstrapVueIcons } from 'bootstrap-vue';
import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap-vue/dist/bootstrap-vue.css';

Vue.use(VueRouter);
Vue.use(BootstrapVue);
Vue.use(BootstrapVueIcons);

const routes = [
	{
		path: '',
		name: 'home',
		component: Initializer
	},
	{
		path: '/',
		name: 'home',
		component: Initializer
	},
	{
		path: '*',
		redirect: '/',
		name: 'home',
		component: Initializer
	},
	{
		path: '/Initializer',
		name: 'Initializer',
		component: Initializer
	},
	{
		path: '/menu',
		name: 'Menu',
		component: MobileMenu
	},
	{
		path: '/items/:category',
		name: 'ItemList',
		component: ItemList,
		meta: {
			title: 'test'
		}
	},
	{
		path: '/m/items/:category',
		name: 'ItemListM',
		component: ItemListMobile
	},
	{
		path: '/about',
		name: 'About',
		// route level code-splitting
		// this generates a separate chunk (about.[hash].js) for this route
		// which is lazy-loaded when the route is visited.
		component: () =>
			import(/* webpackChunkName: "about" */ '../views/About.vue')
	},
	{
		path: '/checkout',
		name: 'Checkout',
		component: Checkout
	}
];

const router = new VueRouter({
	mode: 'history',
	routes
});

export default router;
