<template>
	<div id="itemCell">
		<img :src="`${item.picture}`" class="image" />
		<p class="text">{{ item.long_description }}</p>
		<div class="bottom">
			<p class="text">$ {{ item.unit_price }}</p>
			<qtyControl
				:itemNo="item.item_no"
				@add="addToOrder"
				@minus="removeFromOrder"
			></qtyControl>
		</div>
	</div>
</template>

<script>
import qtyControl from '@/components/sub-components/qtyControl';

export default {
	props: {
		item: {
			type: Object,
			default() {
				return {};
			}
		}
	},
	components: {
		qtyControl
	},
	methods: {
		addToOrder(itemNo) {
			this.$store.dispatch('addToOrder', itemNo);
		},
		removeFromOrder(itemNo) {
			this.$store.dispatch('removeFromOrder', itemNo);
		}
	}
};
</script>

