<template>
	<div>
		<div id="info-header">
			<img id="banner-img" src="@/assets/koi-banner.png" alt="" />
			<div id="info-header-desc">
				<h6>{{ $t('heading-title-1') }}</h6>
				<p>
					{{ $t('heading-msg-1') }}
				</p>
			</div>
			<div id="info-header-seperator"></div>
			<div id="info-header-desc2">
				<p>
					{{ $t('heading-msg-2') }}
				</p>
			</div>
		</div>
		<div id="cat-header-wrapper">
			<b-nav
				b-card-header
				id="cat-header"
				slot="cat-header"
				pills
				v-b-scrollspy="79"
			>
				<b-nav-item
					exact
					exact-active-class="active"
					v-for="cat in categories"
					:key="cat.id"
					:href="'#' + cat.id"
					@click="scrollIntoView"
				>
					<div
						id="tab-content"
						:href="'#' + cat.id"
						@click="$emit('click')"
					>
						<div
							id="tab-icon-wrapper"
							:href="'#' + cat.id"
							@click="$emit('click')"
						>
							<img
								id="tab-icon"
								:src="`${cat.icon}`"
								alt=""
								:href="'#' + cat.id"
								@click="$emit('click')"
							/>
						</div>
						{{ cat.description }}
					</div>
				</b-nav-item>
			</b-nav>
		</div>

		<div id="nav-scroller" ref="content">
			<div v-for="cat in categories" :key="cat.id">
				<div id="nav-scroller-header">
					<div :id="cat.id" class="text">{{ cat.description }}</div>
				</div>
				<div
					v-for="item in items[cat.id]"
					:key="item.item_no"
					@click="handleItemClick(item)"
				>
					<itemCell :item="item"></itemCell>
				</div>
			</div>
		</div>
		<div id="footer"></div>
		<div v-if="orderCount > 0" id="sticky-cart-button" class="align-mid">
			<b-button squared block @click="showCart">
				{{ $t('cart-label') }}({{ orderCount }})
			</b-button>
		</div>
		<b-modal
			id="detail"
			ref="detail"
			:title="selectedItem.description"
			hide-footer
		>
			<div id="modal-wrapper">
				<itemDetail :item="selectedItem" @close="closeDetail" />
			</div>
		</b-modal>
		<b-modal id="cart" ref="cart" :title="$t('cart-label')" hide-footer>
			<div id="modal-wrapper">
				<cartModal />
			</div>
		</b-modal>
	</div>
</template>

<script>
import dataManager from '../../../dataManager';
import itemCell from '@/components/itemCellMobile.vue';
import itemDetail from '@/components/itemDetail';
import cartModal from '@/components/cartModal';
export default {
	data() {
		return {
			categories: [],
			items: {},
			selectedItem: {},
			catIcons: [],
			height: '',
			scrolling: false,
			navHeaderEle: {},
			scrollingTimeOutCheck: {}
		};
	},
	computed: {
		orderCount() {
			return this.$store.getters.orderItems.length;
		}
	},
	components: {
		itemCell,
		itemDetail,
		cartModal
	},
	methods: {
		test() {
			console.log('test');
		},
		handleScrollspyActive(target) {
			let el = document.querySelector(`[href*="${target}"]`);
			if (el) {
				// this.shouldScrollNavHeader = true;
				this.navHeaderEle = el;
			}
		},
		scrollIntoView(event) {
			event.preventDefault();
			const href = event.target.getAttribute('href');
			const el = href ? document.querySelector(href) : null;
			if (el) {
				let headerOffset = 79;
				let elPos = el.getBoundingClientRect().top;
				let offsetPos = elPos + window.pageYOffset - headerOffset;
				window.scrollTo({ top: offsetPos, behavior: 'smooth' });
			}
		},
		async handleItemClick(item) {
			let itemInfocodes = await dataManager.geItemInfocodes(item.item_no);
			let itemUOMs = await dataManager.getItemUOMs(item.item_no);
			if (itemInfocodes.length > 0 || itemUOMs.length > 0) {
				this.showDetail(item);
			} else {
				let itemDetail = {
					qty: 1,
					item_no: item.item_no
				};
				this.$store.dispatch('addToOrderWithDetail', itemDetail);
			}
		},
		showDetail(item) {
			this.selectedItem = item;
			this.$refs['detail'].show();
		},
		closeDetail() {
			this.$refs['detail'].hide();
		},
		showCart() {
			this.$refs['cart'].show();
		},
		handleScrollStop(callback, refresh = 300) {
			let isScrolling;
			window.addEventListener(
				'scroll',
				() => {
					window.clearTimeout(isScrolling);
					isScrolling = setTimeout(callback, refresh);
				},
				false
			);
		}
	},
	async mounted() {
		await dataManager.init();
		this.categories = await dataManager.getAllCategories();
		this.items = await dataManager.allItemsByCat();
	},
	created() {
		this.$root.$on('bv::scrollspy::activate', this.handleScrollspyActive);
		this.handleScrollStop(() => {
			let h =
				document.body.style.getPropertyValue('--app-height-val') * 0.4;
			if (window.pageYOffset < h) {
				let el = document.getElementById('cat-header');
				el.scrollTo({ left: 0, behavior: 'smooth' });
			} else {
				if (this.navHeaderEle) {
					this.navHeaderEle.scrollIntoView();
				}
			}
		});
	},
	destroyed() {}
};
</script>

