<template>
	<div id="cart-modal-container">
		<div id="orders">
			<div v-for="item in orderItems" :key="item.line_no">
				<orderCell
					:operations="true"
					:orderItem="item"
					:infocodes="infocodes"
				></orderCell>
			</div>
		</div>
		<div id="bottom">
			<div id="total">{{$t('total-label')}}: ${{ total }}</div>
			<div id="confirm-btn-wrapper" class="align-mid">
				<b-button squared block id="confirm-btn" v-b-modal.confirmation>
					{{ $t('confirm-btn-label') }}
				</b-button>
			</div>
		</div>
		<b-modal id="confirmation" :title="$t('confirm-btn-label')" @ok="confirmOrder" :ok-title="$t('ok-btn-label')" :cancel-title="$t('cancel-btn-label')"> 
			<p class="my04">{{ $t('confirm-msg') }}</p>
		</b-modal>
		<!-- <b-modal id="qr" ref="qr" title="Show to cashier" hide-footer centered fullscreen modal-class="modal-fullscreen">
			<div style="display: flex; align-content: center; justify-content: center">
				<qr text="12345" :size="qrSize" />
			</div>
		</b-modal> -->
	</div>
</template>

<script>
import orderCell from '@/components/orderCell';
import dataManager from '../dataManager';
// import qr from 'vue-qr';
export default {
	components: {
		orderCell
		// qr
	},
	computed: {
		orderItems() {
			return this.$store.getters.orderItems;
		},
		infocodes() {
			return this.$store.getters.infocodes;
		},
		total() {
			let t = 0;
			this.$store.getters.orderItems.map(line => {
				let p = line.unit_price;
				let infocodes = this.$store.getters.infocodes.filter(
					x => x.item_line_no === line.line_no
				);
				infocodes.map(infocode => {
					if (infocode.subcode_object.amount) {
						p += infocode.subcode_object.amount;
					}
				});
				t += line.quantity * p;
			});
			return parseFloat(t).toFixed(2);
		},
		qrSize() {
			return document.body.style.getPropertyValue('--app-width') - 100;
		}
	},
	methods: {
		async confirmOrder() {
			// this.$store.dispatch('addTableOrder');
			let transactionId = await dataManager.placeOrderAndGetTransactionNo();
			this.$store.dispatch('setTransactonId', transactionId);
			this.$router.replace('/checkout');
		}
	}
};
</script>

